<template>
  <popup-layout
    :title="'OFF '"
    :popupClass="['popup_box','alarm_off_popup_box']"
    :isOpen="isOpen"
    @onClose="onPopupClose"
  >

                <div class="popup_bd">
                    
                    <div class="popup_left_box">
                        <div class="inner">

                            <div class="input_group select_date_btn_group scroll_box">
                                <!--   btn acive . -->
                                <div class="input_box" v-for="day in getWeekDates()" :key="day.value">
                                    <button 
                                      :class="['btn btn_mx btn_secondary_outline',fnSelectedDate(day.value)]" 
                                      @click="onDateSelected(day.value)">{{ day.text }}</button> <!-- active -->
                                </div>
                            </div>

                        </div>
                    </div>

                    <div class="popup_right_box">

                        <!--  -->
                        <default-table
                            :tblClass="['fixed_table_box alarm_off_table']"
                            :headers="cctv_headers"
                            :items="cctvAlarmOffSchedule"
                        >
                        </default-table>

                        <!--  -->

                    </div>

                </div>

  </popup-layout>
</template>
<script>
  import PopupLayout from './PopupLayout'
  import DefaultTable from './DefaultTable'
  import {mapState, mapActions} from 'vuex'
  import dayjs from 'dayjs'
  import 'dayjs/locale/ko'
  import CMMCD from '@/utils/commonCode'
  export default {
    name: 'CctvAlarmOff',
    components: {
      PopupLayout,
      DefaultTable,
    },
    props: {
      isOpen: Boolean
    },
    data() {
        return {
            yymmdd: '',
            cctv_headers: [
                {id: 'CCTV_ID', name: 'CCTV ID', cols_class:"w18p"},
                {id: 'INSTL_LC_ABRV', name: '', cols_class:"w22p"},
                {id: 'BEGIN_HM', name: '', cols_class:"w14p", cell_template: this.getTemplateBeginHm},
                {id: 'END_HM', name: '', cols_class:"w14p", cell_template: this.getTemplateEndHm},
                {id: 'ALAR_CYCLE_CODE', name: '', cols_class:"w22p", cell_template: this.getTemplateCycle},
                {id: 'SCAN_SE_NM', name: '', cols_class:"w20p", cell_template:this.getTemplateScan},
            ],
            items: [],
            selectedDate: dayjs(new Date()).format("YYYYMMDD"),
            IA_EVENT_MLSFC_CODE: CMMCD.IA_EVENT_MLSFC_CODE,
            ALAR_CYCLE_CODE: CMMCD.ALAR_CYCLE_CODE
        }
    },
    computed: {
      ...mapState('cctv', ['cctvAlarmOffSchedule']),
    },
    created() {
      console.log(CMMCD)
      // this.selectedDate=dayjs(new Date(), "YYYYMMDD")
      // this.getAlarmOffSchedule({yyyymmdd:'20211222'})
    },
    methods: {
      ...mapActions('cctv', ['getAlarmOffSchedule']),
      getTemplateBeginHm(row, v) {
        dayjs.locale("ko")
        if (row.ALAR_CYCLE_CODE == 'N') {
          return dayjs(`${row.BEGIN_DE} ${row.BEGIN_HM}`, "YYYYMMDD HHmm").format("HH:mm")
        } else {
          return dayjs(`19000101 ${row.BEGIN_HM}`, "YYYYMMDD HHmm").format("HH:mm")
        }
      },
      getTemplateEndHm(row, v) {
        dayjs.locale("ko")
        if (row.ALAR_CYCLE_CODE == 'N') {
          return dayjs(`${row.END_DE} ${row.END_HM}`, "YYYYMMDD HHMM").format("HH:mm")
        } else {
          return dayjs(`19000101 ${row.END_HM}`, "YYYYMMDD HHmm").format("HH:mm")
        }

      },
      getTemplateCycle(row, v){
        try {
          return  this.ALAR_CYCLE_CODE[v]
        }catch(error) {
          return v
        }
      },
      getTemplateScan(row,v) {
        try {
          return this.IA_EVENT_MLSFC_CODE
                    .filter(item => {
                      return v.split(",").includes(item.id)
                    })
                    .map(item => {
                      return item.name
                    })
                    .join(",")
        } catch(error) {
          return v
        }
      },
      //   (1223())
      getWeekDates() {
        dayjs.locale("ko")
        let _dt = dayjs(new Date())

        let _date = []
        for(let i=0; i<6; i++) {
          let addDate = _dt.add(i, "d")
          _date.push({
            value: addDate.format("YYYYMMDD"),
            text:addDate.format("MMDD(dd)")
          })
        }
        return _date
      },
      fnSelectedDate(v) {
        return this.selectedDate == v ? 'active' : ''
      },
      onDateSelected(v) {
        this.selectedDate = v
        this.getAlarmOffSchedule({yyyymmdd:v})
      },
      onPopupClose() {
        this.$emit("onClose", false)
      },
    },
    watch: {
      isOpen(v) {
        if(v){
          this.onDateSelected(this.selectedDate)
        }
      }
    }

  }

</script>


