<template>
    <div class="cctv_box">
        <div class="lb-wrap">
            <div class="lb-canvas" @click="videoPlayer" v-if="canvasDisplay">
                <VideoCanvas :propsCanvas="canvasDate"/>
            </div>
            <div class="lb-video">                
              <!--<video-player
                    class="vjs-custom-skin videosa"
                    ref="videoPlayer"
                    customEventName="customstatechangedeventname"
                    :options="playerOptions"                                        
                >
                </video-player>-->
                <video id="eventVideo" class="vjs-custom-skin videosa cctvBox" ref="videoPlayer" height="560" width="996">
                    <source :src="playVideoUrl" type="application/x-mpegURL" >
                </video>
            </div>
        </div>
         
    </div>
</template>
<script>
import VideoCanvas from '@/components/event/video/VideoCanvas.vue'
import Hls from "hls.js";

export default {
  props : {
      videoUrl : {
            type : String
        ,   required : false
      }
  ,   xCode : {
            type : Number
        ,   required : false        
      }
  ,   yCode : {
            type : Number
        ,   required : false
      }
  ,   rolWidth : {
            type : Number
        ,   required : false
      }
  ,   rowHeight : {
            type : Number
        ,   required : false
      }
  ,   scanArea : {
          type : Boolean            
      }
  
  },
  components: {
    VideoCanvas
  },
    data() {
      return {        
        videoPlayStatus: "play"
      }
    },
    watch : {
      playVideoUrl(val,bfVal){
        if(this.isNull(val) == false)
        {
          if(Hls.isSupported())
          {
            const hls = new Hls();
            hls.loadSource(val);          
            hls.attachMedia(this.player)
            // hls.on(Hls.Events.MANIFEST_PARSED,()=>{
            //   videoEl.play();
            // })                    
            this.player.play()
            this.videoPlayStatus = "play"
          }
        }        
      }
    },
    computed: {
      player() {
        return this.$refs.videoPlayer
      },
      playVideoUrl(){
          return this.videoUrl
      },
      playerOptions(){
        return {
          width: '996',
          height: '560',
          autoplay: true,
          muted: true,
          language: 'en',
          sources: [
            {
              type: "application/x-mpegURL"
              ,src: this.videoUrl
            }
          ],          
          controlBar :false
        }
      },
      canvasDate(){
        return { 
            _x: this.xCode,  //  x
            _y: this.yCode,  //  y
            _w: this.rolWidth,  //  width
            _h: this.rowHeight,  //  
        }
      },
      canvasDisplay(){        
        return this.scanArea
      }

    },
    methods: {   
      videoPlayer()
      {    
          if(this.videoPlayStatus == "stop")
          {
            this.player.play()
            this.videoPlayStatus = "play"
          }
          else
          {
            this.player.pause()
            this.videoPlayStatus = "stop"
          }          
      }
    , isNull(str)
      {
          if(str == undefined || str == "" || str == null)
          {
              return true;
          }   
          else
          {
              return false;
          }
      }
    }
  }
</script>
<style>
    .lb-wrap {
    width: inherit;
    height: "560px";        
    position: relative;
    }
    .lb-video {
      z-index: 1;
    }
    .lb-canvas {
      width: inherit;
      height: inherit;
      position: absolute;
      z-index: 2;
    }
</style>


