<template>                  

  <div :class="_class">
      <label :class="labelClass" v-for="(item,index) in items" :key="index">
          <input type="checkbox" :value="item[item_id]" v-model="v"  @change="onCheckbox">
          <span>{{item[item_text]}}</span>
      </label>
  </div>
</template>
<script>
  export default {
    name: 'CheckBox',
    props: {
      value: {
        type: [Array,String],
        default: () => { return []},
      },
      type: {
        type: String,
        default: 'v'
      },
      item_id: {
        type: String,
        default: 'id',
      },
      item_text: {
        type: String,
        default: 'text'
      },
      items: [],
      _class: {
        type: Array,
        default:() => {
          return ['input_group chk_group dis_ib']
        }
      },
      labelClass: {
        type: Array,
        default:() => {
          return ['input_box chk_box dis_b']
        }
      }
    },
    data() {
      return {
            v: [...this.value]
        }
    },
    computed: {
      div_class() {
        return 'input_group chk_group dis_ib';
      },
      label_class() {
        return 'input_box chk_box dis_b';
      }
    },
    methods: {
        onCheckbox(){
            this.$emit("input", this.v)
        }
    },

  }

</script>


