<template>
  <canvas width="996" height="560" class="canvas"></canvas>
</template>

<script>
export default {
  
  props: {
    propsCanvas : {
        type : Object    
    }
  },
  mounted() {
    this.drawCanvas(this.propsCanvas)
  },
  methods :{
    drawCanvas(obj){
      this.ctx = this.$el.getContext('2d')    
      this.ctx.strokeStyle = "red"
      this.ctx.lineWidth =4    
      //this.ctx.strokeRect(obj._x, obj._y, obj._w, obj._h)      
      this.ctx.strokeRect(obj._x * 996 , obj._y * 560, obj._w * 996, obj._h * 560)
      
      //this.ctx.beginPath()
      //this.ctx.moveTo(this.propsCanvas._x, this.propsCanvas._y)
      //this.ctx.lineTo(127, 250)
      //this.ctx.lineTo(407, 250)
      //this.ctx.lineTo(407, 450)
      //this.ctx.lineTo(127, 450)
      //this.ctx.lineTo(160, 170)
      //this.ctx.lineTo(30, 170)    
      //this.ctx.lineTo(this.propsCanvas._x, this.propsCanvas._y)

      this.ctx.closePath()
      this.ctx.stroke()
    }
  },
  watch :{
    propsCanvas(val){
        this.drawCanvas(val)
    }
  } 
}
</script>

<style scoped>
/* .canvas {
  border: 1px solid #000;
} */
</style>


