<template>
  <select class="select" v-model="value" @input="$emit('input', $event.target.value)">
      <option value="" v-if="title">{{title}}</option>
      <option v-for="(item, index) in items" :value="item[optionValue]" :key="index">
      {{ item[optionText] }}
      </option>
  </select>
</template>
<script>
  export default {
    name: 'selectBox',
    props: {
      value: {
        type: [String, Number],
        default : ''
      },
      optionValue: '',
      optionText: '',
      items: [],
      title: ""
    },
    methods: {
      onChange(){
        // this.$emit("input", )
      }
    }
  } 
</script>


