<template>
  <PopupLayout
      :title="''"
      :popupClass="['popup_box','alarm_popup_box']"
      :isOpen="isOpen"
      @onClose="onAllAlarmPopupClose"      
  >
   <div class="popup_bd">
      
      <div class="popup_left_box">
          <div class="inner">

              <div class="alarm_row">

                  <span class="alarm_row_label main_label"></span>
                  <RadioBox 
                      v-model="params.alarAt"
                      :items="itemsAlarmAt"
                  ></RadioBox>

              </div>
              <!-- //  ON  -->
              <div class="alarm_row alarm_off_obj"  v-show="!isAlarm">

                  <span class="alarm_row_label"></span>
                  <RadioBox 
                      type='v'
                      v-model="params.alarCycleCode"
                      :items="itemsAlarCycleCode"
                  ></RadioBox>

              </div>
              <div class="alarm_row alarm_off_obj" v-show="!isAlarm && ['W'].includes(inAlarCycleCode)">
                  <span class="alarm_row_label"></span>
                  <check-box
                    v-model="params.reptitDotwNm"
                    :items="itemsReptitDotwNm"
                    :_class="['input_group chk_group dis_ib chk_week']"
                    :labelClass="['input_box chk_box']"
                  ></check-box>
              </div>
              <div class="alarm_row" v-if="isAlarm || ['N'].includes(inAlarCycleCode)">
                  <span class="alarm_row_label"></span>
  
                  <div class="input_group datetime">
                    <!-- <input type="text" class="input_txt input_date" v-model="startDateInput" @click="fnOpen('startDate')"> -->

                    <datepicker 
                        :language="lang" 
                        :format="customFormatter" 
                        v-model="params.beginDe"
                        class="input_box dev_dt ml0" 
                        :style="{'fontSize':'16px'}"
                        ref="startDatePicker"
                        :inline = "false"
                        calendar-button-icon="btn-calendar"
                        :calendar-button="false"
                        placeholder="">
                    </datepicker>
<!--                    <input-hm divClass="input_box time" v-model="params.beginHm" /> -->
                      <select-hm v-model="params.beginHm" />

                          
                </div> 
  
                  
              </div>
              <div class="alarm_row"  v-if="!isAlarm && ['W','D'].includes(inAlarCycleCode)">
                  <span class="alarm_row_label"></span>
                  
                    <div class="input_group datetime">
                      <!-- <input-hm divClass="input_box time ml2" v-model="params.beginHm" /> -->
                      <select-hm v-model="params.beginHm" />
                  </div>
              </div>

              <div class="alarm_row"  v-if="isAlarm || ['N'].includes(inAlarCycleCode)">
                  <span class="alarm_row_label"></span>
                  <div class="input_group datetime">
                      <datepicker 
                            :language="lang" 
                            :format="customFormatter" 
                            v-model="params.endDe"
                            class="input_box dev_dt ml0" 
                            style="font-size:16px"
                            ref="startDatePicker"
                            :inline = "false"
                            calendar-button-icon="btn-calendar"
                            :calendar-button="false"
                            placeholder="">
                        </datepicker>
                        <select-hm v-model="params.endHm" />
                  </div>

              </div>
              <div class="alarm_row"  v-if="!isAlarm && ['W','D'].includes(inAlarCycleCode)">
                  <span class="alarm_row_label"></span>
                  <div class="input_group datetime">
                    
                      <select-hm v-model="params.endHm" />
                  </div>
              </div>

              <div class="alarm_row alarm_off_obj" v-show="!isAlarm">
                  <span class="alarm_row_label"></span>
                  <check-box
                    
                    v-model="params.scanSeNm"
                    :items="itemsScanSeNm"
                    :labelClass="['input_box chk_box mt0 mb0 mr10']"
                  ></check-box>

              </div>
              <!-- 
                   ON   alarm_on_obj  display: block
                   OFF   alarm_off_obj  display: block
                -->
                <!--  ON  -->
              <div class="alarm_row alarm_on_obj" v-show="isAlarm">

                  <span class="alarm_row_label wide_label"></span>
                  <check-box
                    v-model="params.exceptScanSeNm"
                    :items="itemsScanSeNm"
                  ></check-box>

              </div>
              <div class="alarm_row alarm_on_obj" v-show="isAlarm">

                  <span class="alarm_row_label wide_label"></span>
                  <check-box
                    v-model="params.exceptTm"
                    :items="itemsExceptTm"
                  ></check-box>

              </div>
                <!-- //  ON  -->

              <div class="alarm_confirm_obj">
                  <p class="alarm_alert_txt alarm_on_obj" v-show="isAlarm">*  .</p>
                  <p class="alarm_alert_txt alarm_off_obj" v-show="!isAlarm">*  CCTV   </p>
                  <div class="input_group txt_c">
                      <div class="input_box dis_ib">
                          <button class="btn btn_md btn_primary" @click="onConfirm"></button>
                      </div>
                      <div class="input_box dis_ib ml5">
                          <button class="btn btn_md btn_primary_outline" @click="onCancel"></button>
                      </div>
                  </div>
              </div>

          </div>
      </div>

      <div class="popup_right_box">

          <!--  -->
          <!--        colgroup   . -->
          <!--  2    .  tr   table_hd_category . -->
          <DefaultTable
            :tblClass="['fixed_table_box', 'alarm_table']"
            :headers="hCctvList"
            :items="itemsCctv"
            @cellClick="onCellClick"
          ></DefaultTable>
          
          <!--  -->

      </div>

  </div>
  </PopupLayout>
</template>
<script>
  
  import {mapActions} from 'vuex'
  import PopupLayout from '@/components/cctv/PopupLayout'
  import CheckBox from '@/components/cctv/common/CheckBox'
  import RadioBox from '@/components/cctv/common/RadioBox'
  import InputHm from '@/components/cctv/common/InputHm'
  import DefaultTable from '@/components/cctv/DefaultTable'
  import {ko ,en} from 'vuejs-datepicker/dist/locale';
  import Datepicker from 'vuejs-datepicker'
  import dayjs from 'dayjs'
  import SelectBox from '@/components/common/SelectBox.vue'
  import SelectHm from '@/components/cctv/common/SelectHm.vue'

  export default {
    name: 'PopupAlarm',
    components: {
      PopupLayout,
      CheckBox,
      RadioBox,
      DefaultTable,
      InputHm,
      Datepicker,
      SelectBox,
      SelectHm
    },
    props: {
      isOpen: false,
      cctv_id: '',
      title: '',
      popupClass: {
        type: Array,
        default: () => {
          return ['popup_box']
        }
      },
      isOpen: Boolean,
      items:[]
    },
    computed: {
      p () {
        return this.params.endHm
      },
      isAlarm() {
        return this.params.alarAt == 'N'? false:  true
      },
      inAlarCycleCode() {
        return this.params.alarCycleCode
      }
    },
    data() {
      return {
        lang:ko,
        itemsAlarmAt:[ //
          {id:'N', text:'OFF'}, 
          {id:'Y', text:'ON'}
        ],
        itemsAlarCycleCode: [
          {id:'N', text: ''},
          {id:'D', text: ''},
          {id:'W', text: ''}
        ],
        //  ScanTyCode
        itemsScanSeNm: [
          {id:'001', text:''},
          {id:'002', text:''},
          {id:'003', text:''}
        ],
        itemsExceptTm: [
          {id:'T01', text:''},
          {id:'T02', text:''},
          {id:'T03', text:''},
          {id:'T04', text:''}
        ],
        itemsReptitDotwNm: [
          {id:'mon', text:''},
          {id:'tue', text:''},
          {id:'wed', text:''},
          {id:'thu', text:''},
          {id:'fri', text:''},
          {id:'sat', text:''},
          {id:'sun', text:''},
        ],
        beginDe:dayjs(new Date()).format("YYYY-MM-DD"),
        beginHm:'1234',
        endHm:'0000',
        params: {
          alarAt: 'N',         // ON/OFF
          alarCycleCode: 'N',  //    'D', 'W'
          scanSeNm:[],         //,
          reptitDotwNm: [],    //
          exceptScanSeNm:[],   //
          exceptTm: [],        //
          beginDe: dayjs(new Date()).format("YYYY-MM-DD"),          //
          beginHm: dayjs(new Date()).format("HHmm"),          //
          endDe:dayjs(new Date()).format("YYYY-MM-DD"),            //
          endHm:dayjs(new Date()).format("HHmm"),            //
        },
        hCctvList: [
          {id:"CCTV_ID", name:"CCTV ID", cols_class:"w19p", cell_template: this.getTemplateId},
          {id:"INSTL_LC_ABRV", name:"", cols_class:"w29p"},
          {id:"NONE1", name:"", cols_class:"w14p"},
          {id:"act", name:"", cols_class:"w23p", type:'Button', label:''},
        ],
        itemsCctv: this.items
      }
    },
    methods: {
      ...mapActions('cctv', ['postAlarmOff']),
      customFormatter(date)
      {
          //    
          return dayjs(date).format('YYYY-MM-DD');
      },
      onAllAlarmPopupClose(){
        this.$emit("onClose", false)
      },
      getTemplateId(row, v) {
        return `<a href="javascript:void(0)">${v}</a>`
      },
      onCellClick(row, v) {
        if(v == 'act') {
          //  
          this.itemsCctv = this.itemsCctv.filter(item => {
            return item.CCTV_ID != row.CCTV_ID
          })
        } else if (v == 'CCTV_ID' && $(event.currentTarget).find("span")) {
            // CCTV   
            // this.onAllAlarmPopupClose()
            this.$parent.onMangerPopup(row.CCTV_ID)
          
        }
      },
      // this.params 
      initParams() {
        if(this.isAlarm) {
          this.params = {
            ...this.params,
            alarCycleCode: '',   //    'D', 'W'
            scanSeNm:[],         //,
            reptitDotwNm: [],    //
          }
        } else {
          this.params = {
            ...this.params,
            exceptScanSeNm:[],   //
            exceptTm: [],        //
          }
          
        }
      },
      async onConfirm() {
        
        this.initParams()

        if(!this.isAlarm) {

          if(this.params.alarCycleCode == 'N') {
            if(!/[0-9]{4}[0-9]{2}[0-9]{2}/.test(dayjs(this.params.beginDe).format("YYYYMMDD"))) {
              this.$info(' ')
              return false
            }
            if(!/[0-9]{4}[0-9]{2}[0-9]{2}/.test(dayjs(this.params.endDe).format("YYYYMMDD"))) {
              this.$info(' ')
              return false
            }
          }
          if(['W','D','N'].includes(this.params.alarCycleCode)) {
            if(!this.params.beginHm) {
              this.$info(' ')
              return false
            }
            if(!this.params.endHm) {
              this.$info(' ')
              return false
            }
          }
          if(['W'].includes(this.params.alarCycleCode)) {
            if(this.params.reptitDotwNm.length <= 0) {
              this.$info(' ')
              return false
            }
          }

          if (this.params.scanSeNm.length <= 0){
            this.$info(' ')
            return false
          }
        
        } else {
          if(this.params.exceptScanSeNm.length <= 0) {
            this.$info('  1  ')
            return false
          }
          if(this.params.exceptTm.length <=0){
            this.$info('  1  ')
            return false
          }
        }


        const chk = await this.$confirm(' ?')
        if(chk) {
          const cctvIds = this.itemsCctv.map(item => { 
            return item.CCTV_ID

            })
          const params = {
              ...this.params,
              beginDe: ['D','W'].includes(this.params.alarCycleCode) && !this.isAlarm ?'': dayjs(this.params.beginDe).format("YYYYMMDD"),
              endDe: ['D','W'].includes(this.params.alarCycleCode) && !this.isAlarm ? '': dayjs(this.params.endDe).format("YYYYMMDD"),
              reptitDotwNm: ['W'].includes(this.params.alarCycleCode) && !this.isAlarm ? this.params.reptitDotwNm: [],
              cctvIds:cctvIds
            }
          
          this.postAlarmOff(params).then(res => {
            this.$info(' ')

          })
        }
      },
      onCancel() {
        this.onAllAlarmPopupClose()
      },
      onChangeHm(key) {
        const v = event.target.value.replace(/[^0-9]/g,"")
        const vv = v.slice(0,4)
        
        this.params[key] = vv.replace(/([0-9]{2})([0-9]{2})/,"$1:$2")
      }
    },
    watch: {
      items(v) {
        this.itemsCctv = [...v]
      },
    }
  }
</script>
<style>
  .vdp-datepicker.input_box.dev_dt input { 
    width:100%; 
    height:30px;
    font-size:14px;
    color:#666;
    padding-left: 15px;
  }

</style>
<style scoped>
  .alarm_confirm_obj {
    z-index:1
  }
</style>


