<template>
    <span class="ml5">
       <div :class="divClass">
            <input type="number" class="input_txt dev_v1" min="0" max="24" v-model="v1" @keyup="onChangeHH('v1')">
        </div>
        <span class="ml5 mr5">:</span>
        <div :class="divClass">
            <input type="number" class="input_txt dev_v2"  min="0" max="59" v-model="v2" @keyup="onChangeHH('v2')">
        </div>
    </span>
</template>

<script>

    import dayjs from 'dayjs'
    export default {
        name :'InputHm',
        props: {
            value: '',
            divClass: ''
        },
        data() {
            return {
                v1: this.value.slice(0,2),
                v2: this.value.slice(2,4),
            }
        },
        methods:{
            onChangeHH(key) {
                const temp = event.target.value.replace(/[^0-9]/g,"")
                

                if(key == "v1" && temp.length >= 2) {
                    this[key] = temp.slice(0,2)
                    event.target.closest("span.ml5").querySelector("input.dev_v2").focus()
                } else if (key == 'v2') {
                    this[key] = temp.slice(0,2)
                }
                const hhmm = `${this.v1}${this.v2}`
                this.$emit("input", hhmm)
            }
        },
        watch: {

        }
    }
</script>



