<template>
    <div class="popup_wrap">
        <div class="popup_box cctv_popup_box">
           <div class="popup_hd">
                <h3 class="pop_title"> </h3>
                <div class="input_box popup_top_btn">
                    <button class="btn btn_wh btn_smd" @click="callCctvPop">CCTV </button>
                </div>
                <a href="javascript:void(0)" class="btn_pop_close" @click="fnPopClose"><img src="/static/EXcode/static/images/ic_close.png" alt=""></a>
           </div>
           <div class="popup_bd">
                <div class="popup_left_box">
                    <div class="popup_desc_box">
                        <h4> {{ eventInfo.INSTL_LC }} / {{eventInfo.SCAN_GRAD}} / {{eventInfo.SCAN_DT}} / {{eventInfo.PRCPT_SITE_NAME}} / {{eventInfo.NIGHT_TIME}} / {{eventInfo.SEASON_NM}}</h4>
                        <label class="input_box chk_box">
                            <input type="checkbox" v-model="scanResultCodeCheck" value="009" ref="scanResultCodeCheck" @click="fnScanResultCodeCheck">
                            <span></span>
                        </label>
                    </div>                    
                    <!-- cctv Video  -->
                    <Video                        
                        :videoUrl="eventInfo.SCAN_VIDO_URL"                        
                        :xCode="eventInfo.SCAN_XCORD"
                        :yCode="eventInfo.SCAN_YCORD"
                        :rolWidth="eventInfo.SCAN_BT"
                        :rowHeight="eventInfo.SCAN_HG"
                        :scanArea="scanAreaShow"
                    >
                    </Video>                    
                </div>
                <div class="popup_right_box">
                    <!-- tab_item  active    -->
                    <!-- tab_menu -->
                    <div class="tab_box type-a">
                        <ul>
                            <li class="tab_item" :class="scanResultCode == '002' && scanResultCodeCheck == false?'active':''" @click="fnScanResultTab('002')"><span></span></li>
                            <li class="tab_item" :class="scanResultCode == '003' && scanResultCodeCheck == false?'active':''" @click="fnScanResultTab('003')"><span></span></li>
                            <li 
                                class="tab_item" :class="scanResultCode == '004' && scanResultCodeCheck == false?'active':''" @click="fnScanResultTab('004')"
                                v-if="scanResultCode !='002'"
                             >
                                <span></span>
                            </li>
                        </ul>
                    </div>
                    <!-- //tab_menu -->                    
                    <!--  -->
                    <div class="basic_table_box control_info">
                        <div class="table_wrap">
                            <table>
                                 <colgroup>
                                    <col class="w25p">
                                    <col class="w25p">
                                    <col class="w25p">
                                    <col class="w25p">
                                </colgroup>
                                <!--, -->
                                <tbody v-if="scanResultCode == '002' || scanResultCode == '003'">
                                    <template
                                        v-for="(row,rowIndx) in scanTyRowCount"                                        
                                    >
                                        <tr :key="rowIndx">
                                            <th :rowspan="scanTyRowCount+1" v-if="rowIndx == 0"><span>{{scanTyName}}</span></th>
                                            <td colspan="3">                                                
                                                <div class="input_group">
                                                    <template
                                                        v-for="(col,colIdx) in getScanTyCodeList"                                                        
                                                    >
                                                        <label class="input_box radio_box" :key="colIdx" v-if="col.listIndex == rowIndx">
                                                            <input type="radio" name="scanDetailTyCode" :value="col.eventDetailTyCode" ref="eventDetailTyNm" v-model="scanDetailTyCode" @click="fnScanTyChage('tyCode')" :disabled="scanResultCodeCheck==true? true: (popCallType=='EVT')? false :true">                                                            
                                                            <div class="btn btn_secondary_outline btn_md">{{col.eventDetailTyNm}}</div>
                                                        </label>
                                                    </template>
                                                </div>                                                                                            
                                            </td>
                                        </tr>                                        
                                    </template>
                                    <tr class="exception_td_01">
                                        <td colspan="3">
                                            <span></span>
                                            <div class="input_box">
                                                <input type="text" class="input_txt" v-model="scanDetailEtcCn" ref="scanDetailEtcCn" @click="fnScanTyChage('ETC')" :disabled="scanResultCodeCheck==true? true:(popCallType =='EVT')? false :true">
                                            </div>
                                        </td>
                                    </tr>
                                    <tr>
                                        <th><span></span></th>
                                        <td colspan="3">
                                            <div class="input_box">
                                                <input type="text" class="input_txt" v-model="scanResultMemoCn" ref="scanResultMemoCn" :disabled="popCallType == 'EVT'? false : true">
                                            </div>
                                        </td>
                                    </tr>
                                    <tr class="exception_td_02" v-if="eventInfo.SCAN_GRAD == '1'">
                                        <th><span> </span></th>
                                        <td colspan="3">
                                            <div class="input_box">
                                                <input type="text" class="input_txt" v-model="alarPrvnTime" ref="alarPrvnTime" :disabled="popCallType == 'EVT' ? false : true">
                                            </div>
                                            <span>    .</span>
                                        </td>
                                    </tr>
                                </tbody>
                                <!--, -->
                                <!--  -->
                                 <tbody v-if="scanResultCode == '004'">
                                    <tr
                                        v-for="(cctvRow,cctvRowIdx) in getEnvrnCCtvData.codeLen"                                            
                                        :key="'a'+cctvRowIdx"
                                    >
                                        <th   
                                            v-if="cctvRowIdx == 0" 
                                            :rowspan="getEnvrnCCtvData.codeLen"
                                        >
                                        <span>CCTV</span>
                                        </th>
                                        <td colspan="3">                                                
                                            <div class="input_group">
                                                <template
                                                    v-for="(cctvCode,cctvIdx) in getEnvrnCCtvData.codeList"                                                        
                                                >
                                                    <label class="input_box radio_box" :key="cctvIdx" v-if="cctvCode.listIndex == cctvRowIdx">
                                                        <input type="radio" name="envrnFactorCode" :value="cctvCode.eventDetailTyCode" v-model="envrnFactorCode" :disabled="scanResultCodeCheck==true? true: (popCallType=='EVT')? false :true">                                                            
                                                        <div class="btn btn_secondary_outline btn_md">{{cctvCode.eventDetailTyNm}}</div>
                                                    </label>
                                                </template>
                                            </div>                                                                                            
                                        </td>
                                    </tr>
                                    <tr
                                        v-for="(envRow,envRowIdx) in getEnvrnEnvData.codeLen"                                            
                                        :key="'b'+envRowIdx"
                                    >
                                        <th   
                                        v-if="envRowIdx == 0" 
                                        :rowspan="getEnvrnEnvData.codeLen"
                                        >
                                        <span></span>
                                        </th>
                                        <td colspan="3">                                                
                                            <div class="input_group">
                                                <template
                                                    v-for="(envEnvCode,envEnvCodeIdx) in getEnvrnEnvData.codeList"                                                        
                                                >
                                                    <label class="input_box radio_box" :key="envEnvCodeIdx" v-if="envEnvCode.listIndex == envRowIdx">
                                                        <input type="radio" name="envrnFactorCode" :value="envEnvCode.eventDetailTyCode"  v-model="envrnFactorCode" :disabled="scanResultCodeCheck==true? true: (popCallType=='EVT')? false :true">                                                            
                                                        <div class="btn btn_secondary_outline btn_md">{{envEnvCode.eventDetailTyNm}}</div>
                                                    </label>
                                                </template>
                                            </div>                                                                                            
                                        </td>
                                    </tr>
                                    <tr
                                        v-for="(roadRow,roadRIdex) in getEnvrnRoadData.codeLen"                                            
                                        :key="'c'+roadRIdex"
                                    >
                                        <th   
                                        v-if="roadRIdex
                                         == 0" 
                                        :rowspan="getEnvrnRoadData.codeLen"
                                        >
                                        <span></span>
                                        </th>
                                        <td colspan="3">                                                
                                            <div class="input_group">
                                                <template
                                                    v-for="(roadCode,roadCodeIdx) in getEnvrnRoadData.codeList"                                                        
                                                >
                                                    <label class="input_box radio_box" :key="roadCodeIdx" v-if="roadCode.listIndex == roadRIdex">
                                                        <input type="radio" name="envrnFactorCode" :value="roadCode.eventDetailTyCode"  v-model="envrnFactorCode" :disabled="scanResultCodeCheck==true? true: (popCallType=='EVT')? false :true">                                                            
                                                        <div class="btn btn_secondary_outline btn_md">{{roadCode.eventDetailTyNm}}</div>
                                                    </label>
                                                </template>
                                            </div>                                                                                            
                                        </td>
                                    </tr>
                                </tbody>
                                <!--  -->
                            </table>
                        </div>                        
                    </div>
                    <!-- //  -->
                    <div class="input_group w100p txt_c" v-if="this.popCallType == 'EVT'">
                        <div class="input_box">
                            <button class="btn btn_smd btn_primary" @click="fnSave"></button>
                        </div>
                        <div class="input_box">
                            <button class="btn btn_smd btn_primary_outline" @click="fnPopClose"></button>
                        </div>
                    </div>
                    <div class="event_popup_footer">
                        <div class="popup_fott_left">
                            <span></span>
                        </div>
                        <div class="popup_fott_right">
                            <div class="input_group">
                                <label class="input_box radio_box">
                                    <input type="radio" name="scanArea" v-model="scanAreaShow" :value="false">
                                    <span>OFF</span>
                                </label>
                                <label class="input_box radio_box">
                                    <input type="radio" name="scanArea" v-model="scanAreaShow" :value="true">
                                    <span>ON</span>
                                </label>
                            </div>
                        </div>
                    </div>
                </div>
           </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import Video from "@/components/event/video/Video.vue"
export default {
    data() {
        return {
            eventInfo : {}
        ,   scanResultCodeCheck : false
        ,   evtSetupList : this.$store.state.event.eventSetList
        ,   scanResultCode : "002"      // default :              
        ,   scanTyCode : ""             // 
        ,   scanTyName : ""             // 
        ,   scanTyRowCount : 0          //   
        ,   scanDetailTyCode : ""       // 
        ,   scanDetailEtcCn : ""        // 
        ,   envrnFactorCode: "" // 
        ,   scanResultMemoCn: ""   // 
        ,   alarPrvnTime: "0"        // 
        ,   scanAreaShow : true 
        }
    }
,   components: {
        Video
    }
,   props : {
        cctvID : {
            type : String
        ,   required : true
        }
    ,   scanDT : {
            type : String
        ,   required : true
        }
    ,   popCallType : {
            type : String
        ,   required : false
        ,   default : "CCTV"
        }
    }
,   watch : {
        getScanTyCodeList(arr){
            var arrLen = arr.length;
            if(this.scanResultCode != "004")
            {
                if(arrLen % 3 > 0)
                {
                    this.scanTyRowCount = parseInt((arrLen / 3)) +1
                }
                else
                {
                    this.scanTyRowCount = parseInt((arrLen / 3))
                }         
            }
            
        }
    }
,   computed : {
        //  > cctv
        getEnvrnCCtvData()
        {
            var codeList = this.evtSetupList
            var index = 0;
            var envObj = {}
            var arr =[]
            if(codeList && codeList.length > 0)
            {
                for(var i in codeList)
                {
                    var codeObj = codeList[i];
                    if(codeObj.eventSeCode == "004")
                    {
                        var tyNm = "";
                        if(codeObj.eventTyCode == "004")
                        {
                            codeObj.listIndex = index
                            arr.push(codeObj)                            
                            if(arr.length % 3 == 0)
                            {
                                index++;
                            }
                        }                      
                        
                    } 
                }
                var arrLen = arr.length
                var tyRowCount = 0;
                if(arrLen % 3 > 0)
                {
                    tyRowCount = parseInt((arrLen / 3)) +1
                }
                else
                {
                    tyRowCount = parseInt((arrLen / 3))
                } 
                envObj.codeList = arr;
                envObj.codeLen  =   tyRowCount
            }
            return envObj
        }
        //  > 
    ,   getEnvrnEnvData(){
            var codeList = this.evtSetupList
            var index = 0;
            var envObj = {}
            var arr =[]
            if(codeList && codeList.length > 0)
            {
                for(var i in codeList)
                {
                    var codeObj = codeList[i];
                    if(codeObj.eventSeCode == "004")
                    {
                        var tyNm = "";
                        if(codeObj.eventTyCode == "005")
                        {
                            codeObj.listIndex = index
                            arr.push(codeObj)                            
                            if(arr.length % 3 == 0)
                            {
                                index++;
                            }
                        }                      
                        
                    } 
                }
                var arrLen = arr.length
                var tyRowCount = 0;
                if(arrLen % 3 > 0)
                {
                    tyRowCount = parseInt((arrLen / 3)) +1
                }
                else
                {
                    tyRowCount = parseInt((arrLen / 3))
                } 
                envObj.codeList = arr;
                envObj.codeLen  =   tyRowCount
            }
            return envObj
        }   
        //  > 
    ,   getEnvrnRoadData(){
            var codeList = this.evtSetupList
            var index = 0;
            var envObj = {}
            var arr =[]
            if(codeList && codeList.length > 0)
            {
                for(var i in codeList)
                {
                    var codeObj = codeList[i];
                    if(codeObj.eventSeCode == "004")
                    {
                        var tyNm = "";
                        if(codeObj.eventTyCode == "006")
                        {
                            codeObj.listIndex = index
                            arr.push(codeObj)                            
                            if(arr.length % 3 == 0)
                            {
                                index++;
                            }
                        }                      
                        
                    } 
                }
                var arrLen = arr.length
                var tyRowCount = 0;
                if(arrLen % 3 > 0)
                {
                    tyRowCount = parseInt((arrLen / 3)) +1
                }
                else
                {
                    tyRowCount = parseInt((arrLen / 3))
                } 
                envObj.codeList = arr;
                envObj.codeLen  =   tyRowCount
            }
            return envObj
        }
        //  input  
    ,   getScanTyCodeList()
        {   
            var codeList = this.evtSetupList
            var arr = []            
            var index = 0;
            if(codeList && codeList.length > 0)
            {                
                for(var i in codeList)
                {
                    var codeObj = codeList[i];                    
                    if(codeObj.eventSeCode == this.scanResultCode)
                    {
                        if(codeObj.eventTyCode == this.scanTyCode)
                        {   
                            codeObj.listIndex = index
                            arr.push(codeObj)                            
                            if(arr.length % 3 == 0)
                            {
                                index++;
                            }
                        }
                    }
                }                
            }                        
            return arr
        }
    }
,   mounted() 
    {   
        //console.log(this.popCallType)
        this.fnPopSearch()
    }
,   methods : {
        ...mapActions('event', ['getEventPopData','setEventInfo'])
    ,   callCctvPop()
        {
            var cctvId = this.eventInfo.CCTV_ID; 
            this.$parent.eventPopData.popCallType = "CCTV"           
            this.$parent.onMangerPopup(cctvId);
        }
    ,   fnScanResultCodeCheck()
        {
            if(this.scanResultCodeCheck == false)
            {
                // true 
                this.scanDetailTyCode = ""
                this.scanDetailEtcCn =""
                this.envrnFactorCode=""
            }
            
        }
    ,   fnScanTyChage(type)
        {   
            if(type == "ETC")
            {
                this.scanDetailTyCode = "ETC";                
            }
            else
            {
                this.scanDetailEtcCn = "";
            }
        }
    ,   fnScanResultTab(resultCode)
        {   
            //   
            if(this.scanResultCodeCheck) return;            
            //  -> , ->            
            //      
            if(resultCode == "004" && this.scanResultCode == "002") return;                                    
            //     
            if(resultCode == "002" && this.scanResultCode == "004") return;
            
            //    
            if(this.popCallType == 'EVT')
            {                
                //        
                if(resultCode == "002" || (resultCode=="003" && this.scanResultCode =="002"))
                {
                    this.scanDetailTyCode  ="";
                    this.scanDetailEtcCn = "";
                }
                //       
                if(resultCode == "002"){
                    this.envrnFactorCode = "";
                }
            }
            else
            {
                // CCTV   ()
                //        
                if(this.scanResultCode == "003" && resultCode =="002") return;
                //         
                if(this.scanResultCode == "002" && resultCode == "003") return;
            }

            this.scanResultCode = resultCode

        }
        //   
    ,   async fnPopSearch()
        {    
            var pam = {
                cctvId : this.cctvID
            ,   scanDt : this.scanDT
            ,   searchType : this.isNull(this.evtSetupList)? "L" :"R"
            ,   intdCode : this.$store.state.auth.insttId
            }
            const result = await this.getEventPopData(pam);
            var data = result.data;
            var info = data.info;
            this.eventInfo = info;                        
            //              
            if(this.isNull(info.SCAN_RESULT_CODE) == false)
            {
                if(info.SCAN_RESULT_CODE =='009')
                {
                    this.scanResultCode = "002";
                    this.scanResultCodeCheck = true;
                }
                else
                {
                    this.scanResultCode = info.SCAN_RESULT_CODE
                }
                
                //   
                if(this.isNull(info.SCAN_DETAIL_TY_CODE) == false)
                {
                    this.scanDetailTyCode = info.SCAN_DETAIL_TY_CODE;
                    //  
                    if(info.SCAN_DETAIL_TY_CODE  == "ETC")
                    {
                        this.scanDetailEtcCn = info.SCAN_DETAIL_ETC_CN
                    }
                }

                // 
                if(this.isNull(info.ENVRN_FACTOR_CODE) == false)
                {
                    this.envrnFactorCode = info.ENVRN_FACTOR_CODE
                }
                // 
                if(this.isNull(info.SCAN_RESULT_MEMO_CN) == false)
                {
                    this.scanResultMemoCn = info.SCAN_RESULT_MEMO_CN
                }
                // 
                if(info.ALAR_PRVN_TIME > 0)
                {
                    this.alarPrvnTime = info.ALAR_PRVN_TIME
                }
            }

            if(this.isNull(info.SCAN_TY_CODE) == false)
            {
                this.scanTyCode = info.SCAN_TY_CODE
                this.scanTyName = info.SCAN_TY_CODE_NM
            }
            if(this.isNull(this.evtSetupList))
            {
                this.evtSetupList = this.$store.state.event.eventSetList
            }           

        }
    ,   isNull(str)
        {
            if(str == undefined || str == "" || str == null)
            {
                return true;
            }   
            else
            {
                return false;
            }
        }
    ,   async fnSave(){
            if(this.$refs.scanResultCodeCheck.checked == false)
            {
                if(this.isNull(this.scanDetailTyCode) || (this.scanDetailTyCode == "ETC" && this.isNull(this.scanDetailEtcCn)))
                {
                    this.$info(" ");
                    return 
                }          
            }
            var con = await this.$confirm("?")
            if(con)
            {
                this.fnSaveAction();
            }
        }
    ,   async fnSaveAction()
        { 
            var params = {
                cctvId : this.eventInfo.CCTV_ID     // cctvID
            ,   scanDt : this.eventInfo.SCAN_DT     // 
            ,   scanGrade : this.eventInfo.SCAN_GRAD
            ,   scanResultCode : (this.$refs.scanResultCodeCheck.checked)? "009" : (this.scanResultCode == "004")? "003" : this.scanResultCode  // 
            ,   scanDetailTyCode : (this.$refs.scanResultCodeCheck.checked)? null : this.scanDetailTyCode // 
            ,   scanDetailEtcCn : (this.$refs.scanResultCodeCheck.checked)? null : this.scanDetailEtcCn   // 
            ,   scanResultMemoCn: this.scanResultMemoCn         // 
            ,   alarPrvnTime : this.alarPrvnTime                // 
            ,   envrnFactorCode : this.envrnFactorCode          //             
            }
            
            const result = await this.setEventInfo(params)
            
            if(result.status == 200)
            {
                this.$info(".","");
                setTimeout(this.fnPopClose(),5000)
            }
            else
            {
                this.$info("    .")
            }

        }
    ,   fnPopClose()
        {
            this.$emit('close');
        }
    }
}
</script>


