<template>                  
  <!--
    <<< radios >>>>
    div> input_group main_group dis_ib"  
    div> label: 'input_box radio_box' 2.. > ml30
    <<< checkbox >>>
    div> input_group chk_group dis_ib
    div>label: input_box chk_box dis_b
  
  -->
  <div :class="div_class">
      <label :class="[label_class, type=='h' && index > 0? 'ml30': '']" v-for="(item,index) in items" :key="index">
          <input type="radio" :value="item[item_id]" v-model="v" @change="onRadio">
          <span>{{item[item_text]}}</span>
      </label>
      <!--
      <label class="input_box chk_box dis_b">
          <input type="checkbox" name="" id="">
          <span></span>
      </label>
      <label class="input_box chk_box dis_b">
          <input type="checkbox" name="" id="">
          <span></span>
      </label>
      -->
  </div>
</template>
<script>
  export default {
    name: 'RadioBox',
    props: {
      value: '',
      type: {
        type: String,
        default :'h' //:h,: 
      },
      item_id: {
        type: String,
        default: 'id',
      },
      item_text: {
        type: String,
        default: 'text'
      },
      items: [],
      
    },
    data() {
        return {
            v: this.value
        }
    },
    computed: {
      div_class() {
        return this.type == 'h' ? 'input_group main_group dis_ib' : 'input_group chk_group dis_ib';
      },
      label_class() {
        return this.type == 'h' ? 'input_box radio_box' : 'input_box radio_box dis_b';
      }
    },
    methods: {
        onRadio(){
            this.$emit("input", this.v)
        }
    }
  }

</script>


