<template>
    <div class="input_box dev_time">
        <SelectBox v-model="v1"
            :optionValue="'id'"
            :optionText="'name'"
            :items="getRange(24)"
            :title="''"
        >                              
        </SelectBox>
        <SelectBox v-model="v2"
            :optionValue="'id'"
            :optionText="'name'"
            :items="getRange(60)"
            :title="''"
        >                              
        </SelectBox>
    </div>
</template>

<script>
    import SelectBox from '@/components/common/SelectBox.vue'
    export default {
        name: 'SelectHm',
        props: {
            value: {
              type: String,
              default: '0000'
            }
        },
        components: {
            SelectBox
        },
        data () { 
            return {
                v1: this.value.slice(0,2),
                v2: this.value.slice(2,4)
            }
        },
        methods: {
            getRange(num) {
                return Array.from(Array(num).keys())
                    .map(idx => {
                        return {
                            id: String(idx).padStart(2,'0'), 
                            name: String(idx).padStart(2,'0') 
                        }
                    })
            },
        },
        watch:{
          v1(v){
            this.$emit("input", `${this.v1}${this.v2}`)
          },
          v2(v) {
            this.$emit("input", `${this.v1}${this.v2}`)
          }
        }
    }
</script>
<style scoped>

  .input_box.dev_time .select {
    width: 50px ;
    margin-left:1px;
    background-position: 30px center;
    display:inline
  }
</style>


