<template>
       <!-- container -->
       <div :class="['container', eventPopData.open?'event setting': 'cctv']">

           <div class="inner">
                <h2 class="page_title">CCTV </h2>

                <!-- content_box -->
                <div class="content_box">

                    <!-- input       . -->
                    <!-- input_group -->
                    <div class="input_group">
                        <div class="input_box mr30">
                            <!--<select name="" id="" class="select">
                                <option value="">CCTV ID</option>
                            </select> -->
                            <SelectBox v-model="search.cctvId"
                              :optionValue="'CCTV_ID'"
                              :optionText="'CCTV_ID'"
                              :items="commCctvList"
                              :title="'CCTV_ID'"
                            >                              
                            </SelectBox>
                        </div>
                        <div class="input_box mr30">
                            <SelectBox v-model="search.mEnv"
                              :optionValue="'id'"
                              :optionText="'name'"
                              :items="P_ANALS_CND_CODE"
                              :title="''"
                            >                              
                            </SelectBox>
                        </div>
                        <div class="input_box mr30">
          
                            <SelectBox v-model="search.detail"
                              :optionValue="'id'"
                              :optionText="'name'"
                              :items="optionDetail"
                              :title="''"
                            >                              
                            </SelectBox>
                        </div>
                        <div class="input_box mr30">
                            <SelectBox v-model="search.sttNet"
                              :optionValue="'ID'"
                              :optionText="'NAME'"
                              :items="commYN"
                              :title="''"
                            >                              
                            </SelectBox>
                        </div>
                        <div class="input_box mr6">
                          <SelectBox v-model="search.alarAt"
                              :optionValue="'ID'"
                              :optionText="'NAME'"
                              :items="commYN"
                              :title="''"
                            >                              
                            </SelectBox>
                        </div>
                        <div class="input_box mr6">
                            <button class="btn btn_lg btn_primary btn_lg_txt" @click="onSearchList()">
                                <img src="static/EXcode/lib/images/ic_search.png" alt="" class="ic_search">
                                
                            </button>
                        </div>
                        <div class="input_box">
                            <button class="btn btn_lg btn_red btn_lg_txt" @click="onAllAlarmPopupOpen()"></button>
                        </div>
                    </div>
                    <!-- // input_group -->
                    <DefaultTable
                      :tblClass="['fixed_table_box', 'txt_r']"
                      :headers="cctv_headers"
                      :items="cctvList"
                      @cellClick="onCellClick"
                      @sort="onSortClick"
                    >
                      <template slot="table_tool">
                        <div class="input_box dis_ib mt30 mb10">
                            <button class="btn btn_lg btn_lg_txt btn_secondary" @click="fnAlarmOffPopupOpen()">OFF </button>
                        </div>
                      </template>
                    </DefaultTable>
                <!--table-->
                </div>
                <!-- // content_box -->

          </div>

          
          <!-- {{   -->
          <PopupAlarm 
              :isOpen="isAllAlarmOpen"
              @onClose="onAllAlarmPopupClose"
              :items="allCctvList"
            />
          <!--  }} -->
          <!-- {{ off -->
          <PopupAlarmOff
              :isOpen="isAlarmOffOpen"
              @onClose="onAlarmOffPopupClose"
          />
          <!--  off }} -->
       
          <!--{{   >   -->
          <PopupCctv 
              :isOpen="isContorlPopupOpen"
              :cctvId="selectedCctvId"
              :src="videoSrc"
              @onClose="onManagerPopupClose"
              />
          <!--   >  }}-->

        <EventPopup
            v-if="eventPopData.open"
            @close="eventClosePop"
            :cctvID="eventPopData.cctvID"            
            :scanDT="eventPopData.scanDT"            
        />

       </div>




       <!-- container -->
</template>
<script>
  import commonCode from '@/utils/commonCode.js'
  import { mapState, mapMutations, mapActions } from 'vuex'
  import DefaultTable from '@/components/cctv/DefaultTable'
  import PopupCctv from '@/components/cctv/PopupCctv'
  import PopupAlarm from '@/components/cctv/PopupAlarm'
  import PopupAlarmOff from '@/components/cctv/PopupAlarmOff'
  import SelectBox from '@/components/common/SelectBox.vue'
  import EventPopup from "@/components/event/EventPopup"

  import dayjs from 'dayjs'

  export default {
    name: 'cctv',
    components: {
      DefaultTable,
      PopupCctv,
      PopupAlarm,
      PopupAlarmOff,
      dayjs,
      SelectBox,
      EventPopup
    },  
    data () { 
      return {
        videoSrc: '',
        isContorlPopupOpen: false,//cctv   
        isAllAlarmOpen: false, //   
        isAlarmOffOpen: false,
        cctv_headers:[
          {id: 'CCTV_ID', name:'CCTV ID', cols_class:"w12p", isSort:'Y',},
          {id: 'INSTL_LC_ABRV', name:'', cols_class:"w12p", isSort:'Y'},
          {id: 'CCTV_ID2', name:'', cols_class:"w8p", isSort:'Y'  },
          // {id: 'USE_AT', name:'', cols_class:"w10p", isSort:'Y'},
          {id: 'INSTL_DT', name:'', cols_class:"w10p", isSort:'Y', cell_template: this.getTemplateDt},
          // {id: 'INSTL_DRC_CODE', name:'', cols_class:"w8p", isSort:'Y'},
          {id: 'STT_NET', name:'', cols_class:"w8p", isSort:'Y',  cell_template: this.getTemplateSttNet}, //
          {id: 'STT_RNG', name:'', cols_class:"w8p", isSort:'Y', cell_template: this.getTemplateSttRng},
          {id: 'EVENT_CNT', name:'', cols_class:"w8p", isSort:'Y'},
          {id: 'ALAR_AT', name:'', cols_class:"w8p", isSort:'Y', cell_template: this.getTemplateAlarAt},
          {id: 'USE_AT', name:'', cols_class:"w8p", isSort:'Y', cell_template: this.getTemplateAlarAt},
          {id: 'ACTION', name:'', cols_class:"w8p",type:'Button'},

        ],
        // cctv_items:[],
        cctv_sort:[],
        selectedCctvId:null,
        commCctvList: [], // (cctv_id)
        allCctvList: [],
        commYN:[{ID:'Y', NAME:'Y'},{ID:'N', NAME:'N'}],
        search:{
          cctvId:'',
          sttNet:'',
          alarAt: '',
          mEnv: '',
        },
        P_ANALS_CND_CODE: commonCode.P_ANALS_CND_CODE,
        ANALS_CND_CODE:commonCode.ANALS_CND_CODE,
        eventPopData : {
          open : false,
          cctvID : "",
          scanDT : ""
        }
      }
    },
    computed: {
      ...mapState('cctv', ['cctvList']),
      optionDetail() {
        this.search.detail = ''
        return this.ANALS_CND_CODE.filter(item => {
          return item.P_ANALS_CND_CODE == this.search.mEnv
        })

      }
    },
    mounted() {
        this.getCctvList({}).then(res => {
          // cctv_id
          this.commCctvList = res
          $(".scroll_box").mCustomScrollbar({
            theme : "minimal-dark"
          });
        })

    },
    methods: {
      ...mapActions('cctv', ['getCctvList','getAlarmOffSchedule']),
      ...mapActions('event', ['getEventList']),
      getTemplateEventReg (row, v, fn) {
        return "<div class='input_box'>" +
               "   <button class='dis_ib btn btn_md btn_secondary'></button>"+
               "</div>"
      },
      getTemplateDt(row, v) {
        return dayjs(v).format("YYYY-MM-DD")
      },
      getTemplateAlarAt(row, v) {
        return v=='Y'?'ON':'<font color="red">OFF</font>'
      },
      getTemplateSttNet(row, v) {
        return v=='Y'?'':''
      },
      getTemplateSttRng(row, v) {
        return v=='Y'?'':''
      },
      getComponent(row){
        return 'Button'
      },
      onCellClick(row, v) {
        if(v=='ACTION') {
          console.log(row, v)
          this.onMangerPopup(row.CCTV_ID)
        }
        
      },
      onMangerPopup(cctv_id) {
        this.isContorlPopupOpen=true
        this.selectedCctvId = cctv_id
        this.eventPopData.open = false
      },
      onManagerPopupClose(v) {
        this.isContorlPopupOpen=false
      },
      onAllAlarmPopupOpen(cctvId) {
        if(cctvId) {
          this.allCctvList = [...this.commCctvList.filter(item => {return item.CCTV_ID == cctvId})]
        }else{
          this.allCctvList = [...this.commCctvList]
        }
        this.isAllAlarmOpen = true
      },
      onAllAlarmPopupClose(){
        this.isAllAlarmOpen = false
      },
      // off  
      fnAlarmOffPopupOpen() {
        this.isAlarmOffOpen = true
        this.getAlarmOffSchedule({yyyymmdd:dayjs(new Date()).format("YYYYMMDD")})
      },
      // off  
      onAlarmOffPopupClose() {
        this.isAlarmOffOpen = false
      },
      // 
      onSearchList(){
        this.getCctvList(this.search)
      },
      onSortClick(key, updown){
        // client sorting
        let sorting = 1
        if (updown == 'up') {
          sorting = -1
        }
        this.cctvList = this.cctvList.sort((a,b) => {
          if (a[key] > b[key]) return sorting
          if (a[key] < b[key]) return -sorting
          return 0
        })
        // server sorting 
        // this.getCctvList({...this.search, [updown=='up'?'desc':'asc']:key})
      },

      eventOpenPop(obj) {
        this.eventPopData = {
          open : true,
          cctvID : obj.CCTV_ID,
          scanDT : dayjs(obj.SCAN_DT).format('YYYY-MM-DD HH:mm:ss')
        }
      },
      eventClosePop(){
        this.eventPopData.open=false
        // this.onManagerPopupClose()

      },
    }
  }

</script>


